r: #3b3b3b;\r\n            cursor: pointer;\r\n            outline: none;\r\n            padding: 16px 24px;\r\n            text-align: center;\r\n            text-decoration: none;\r\n            user-select: none;\r\n            -webkit-user-select: none;\r\n            width: 100%;\r\n            height: 90%;\r\n            align-self: center;\r\n        }\r\n\r\n        button:disabled, .menu:disabled {\r\n            pointer-events: none;\r\n        }\r\n\r\n        button:hover, .menu:hover, .menu_contents a:hover{\r\n            color: #fff;\r\n            background-color: #1a1a1a;\r\n            box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;\r\n            border-radius: 15px;\r\n        }\r\n\r\n        button:active, .menu:active {\r\n            box-shadow: none;\r\n        }\r\n\r\n        .menu{\r\n            width: 10%;\r\n            height: 90%;\r\n            align-self: center;\r\n            position: absolute;\r\n            right: 1rem;\r\n        }\r\n\r\n        .menu_contents{\r\n            display: none;\r\n            border-radius: 1rem;\r\n            position: absolute;\r\n            flex-direction: column;\r\n            justify-content: center;\r\n            align-content: center;\r\n            background-color: #f1f1f1;\r\n            width: 100%;\r\n            height: 15rem;\r\n            box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);\r\n        }\r\n\r\n        .menu_contents a{\r\n            @include noto-sans-tc($weight: 700);\r\n            font-size: 1.5rem;\r\n            appearance: none;\r\n            background-color: transparent;\r\n            border: none;\r\n            color: #3b3b3b;\r\n            cursor: pointer;\r\n            outline: none;\r\n            text-decoration: none;\r\n            gap: 1rem;\r\n            display: flex;\r\n            justify-content: center;\r\n            align-items: center;\r\n            height: 3em;\r\n            text-align: center;\r\n        }\r\n        .menu:hover .menu_contents {\r\n            display: flex;\r\n        }\r\n    }\r\n}\r\n\r\n// 標題與描述\r\nh2.main-title-gradient {\r\n    font-size: 3rem;\r\n    font-weight: 800;\r\n    background: linear-gradient(to bottom, $main-theme-color, #ffffff);\r\n    -webkit-background-clip: text;\r\n    background-clip: text;\r\n    color: transparent;\r\n    margin: 30px 0;\r\n    margin-top: 15rem;\r\n}\r\n\r\n.description {\r\n    font-size: 1.2rem;\r\n    margin-bottom: 30px;\r\n}\r\n\r\n// 按鈕區塊\r\n.button-container {\r\n    display: flex;\r\n    justify-content: center;\r\n    gap: 20px;\r\n\r\n    .btn {\r\n        background: #2f78ff;\r\n        border: none;\r\n        color: #ffffff;\r\n        padding: 15px 30px;\r\n        font-size: 1rem;\r\n        font-weight: 500;\r\n        border-radius: 8px;\r\n        cursor: pointer;\r\n        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);\r\n        transition: all 0.3s ease;\r\n\r\n        &:hover {\r\n            background: #1a1a1a;\r\n            color: #fff;\r\n            box-shadow: 0 8px 15px rgba(0, 0, 0, 0.25);\r\n        }\r\n\r\n        &:disabled {\r\n            background: #cccccc;\r\n            pointer-events: none;\r\n        }\r\n    }\r\n}\r\n"],"sourceRoot":""}]);
